"use client";

import { Check, Save } from "lucide-react";
import { Button } from "~/components/ui/button";
import { useState } from "react";
import { toast } from "sonner";

interface SummarySectionProps {
  response: string;
  originalText: string;
}

export function SummarySection({
  response,
  originalText,
}: SummarySectionProps) {
  const [isSaving, setIsSaving] = useState(false);
  const [isSaved, setIsSaved] = useState(false);

  // Function to process the response and extract HTML if it's inside code blocks
  const processResponse = (text: string) => {
    // Check if the text contains HTML code blocks
    const htmlCodeBlockRegex = /```html\s+([\s\S]*?)```/g;
    const match = htmlCodeBlockRegex.exec(text);

    // If an HTML code block is found, extract only the HTML content
    if (match && match[1]) {
      return match[1].trim();
    }

    // Otherwise, return the original text
    return text;
  };

  const processedResponse = response ? processResponse(response) : "";

  // Function to save the summary to the database
  const handleSave = async () => {
    if (!response || !originalText || isSaved) return;

    try {
      setIsSaving(true);

      const res = await fetch("/api/summary", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          text: originalText,
          summary: processedResponse,
          saveToDatabase: true,
        }),
      });

      if (!res.ok) {
        throw new Error("Failed to save summary");
      }

      setIsSaved(true);
      toast.success("Resumen guardado", {
        description: "El resumen se ha guardado correctamente.",
      });
    } catch (error) {
      console.error("Error saving summary:", error);
      toast.error("Error", {
        description: "No se pudo guardar el resumen. Inténtalo de nuevo.",
      });
    } finally {
      setIsSaving(false);
    }
  };

  return (
    <div className="w-full max-w-[50vw] min-h-[50vh] 2xl:min-h-screen bg-slate-100 dark:bg-slate-900 p-12 relative">
      {response ? (
        <div className="h-full">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-lg sm:text-xl md:text-2xl font-semibold text-slate-800 dark:text-slate-200 flex items-center">
              <Check className="mr-2 text-green-500" size={20} />
              Resumen Generado:
            </h2>

            <Button
              variant="outline"
              size="sm"
              onClick={handleSave}
              disabled={isSaving || isSaved}
              className="flex items-center gap-2"
            >
              <Save size={16} />
              {isSaving
                ? "Guardando..."
                : isSaved
                ? "Guardado"
                : "Guardar resumen"}
            </Button>
          </div>

          <div className="text-slate-800 dark:text-slate-200 text-base sm:text-lg">
            <div
              dangerouslySetInnerHTML={{ __html: processedResponse }}
              className="whitespace-normal break-words text-justify"
            ></div>
          </div>
        </div>
      ) : (
        <div className="flex items-center justify-center h-full text-slate-400 p-4 sm:p-6">
          <p>El resumen aparecerá aquí</p>
        </div>
      )}
    </div>
  );
}
